<template>
  <section id="dashboard">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="fullPage"></loading>
    <div class="container">
      <div class="row justify-content-between mt-3">
        <h1 class="h1 col-6 justify-content-start">DASHBOARD</h1>
        <div class="d-flex justify-content-end align-items-center pr-4">
          <p class="pr-3 mt-3">Currency</p>
          <b-tabs v-model="tabIndex">
            <b-tab title="NGN" :title-link-class="linkClass(0)"></b-tab>
          </b-tabs>
        </div>
      </div>
      <div class="container jus">
        <div class="row mb-3">
          <div class="col-lg-4 d-flex justify-content-between mb-2">
            <div class="card px-5 pt-4 pb-2 bg-white card-one">
              <p class="b-bold-text">
                Current Balance
                <br>
                <span>{{newTime}}</span>
              </p>
              <h4 class="b-text">
                {{userBalance.currency}}
                <span>{{userBalance.amount}}</span>
              </h4>
            </div>
          </div>
          <div class="col-lg-4 d-flex justify-content-between mb-2">
            <div class=" px-2 pt-4 bg-white card-one">
              <div class="progress-details col">
                <div class="row">
                  <div class="col-md-3">
                    <img src="/img/dashboard-badge.png">
                  </div>
                  <div class="col-md-9 played">
                    <h4>Total Games</h4>
                  </div>
                </div>
                <h4 class="text-center">{{getGames}}</h4>
              </div>
            </div>
          </div>
          <div class="col-lg-4 d-flex justify-content-between mb-2">
            <div class="card px-5 pt-4 bg-white">
              <div class="wins">
                <h5 class="winz">Number Of Wins</h5>
                <h3 class="mt-3 numbers1 text-center">{{getWins}}</h3>
              </div>
            </div>
          </div>
        </div>
        <div class="bg-differnt row">
            <div class="justify-content-between px-5 pt-3 pb-2 col-3">
              <div class="wins">
                <h5 class="active-game">
                  Number of active games
                </h5>
                <h1 class="mt-2 active-game-number">{{getCount}}</h1>
              </div>
            </div>
            <div class="masthead col-9">
              </div>
          </div>
        <div class="row cont mt-4">
          <div class="mainstream pl-4">
            <div class="mt-4">
              <h1 class="result-text">RECENT GAMES</h1>
            </div>
            <div class="justify-content-between">
              <table class="table table-borderless tables">
                <thead>
                  <tr>
                    <th>Game</th>
                    <th>Stake</th>
                    <th>Winnings</th>
                    <th>Bonus</th>
                    <th>Date</th>
                    <th>Status</th>
                    <th>Ticket</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    class="row1" v-for="(pastGame, index) in itemsSuccess.slice()" v-bind:key="index" v-if="index <= 9">
                    <td class="td">{{pastGame.Bet_Details}}</td>
                    <td class="td">{{pastGame.Stake}}</td>
                    <td class="td">{{pastGame.Possible_Winnings}}</td>
                    <td class="td">{{pastGame.Possible_Bonus}}</td>
                    <td class="td">{{pastGame.Date_Placed}}</td>
                    <td v-if="pastGame.status === 'Pending'">
                      <span class="badge warn badge-pill badge-warning">{{pastGame.status}}</span>
                    </td>
                    <td v-else-if="pastGame.status === 'Lost'">
                      <span class="badge lost badge-pill badge-danger">{{pastGame.status}}</span>
                    </td>
                    <td v-else>
                      <span class="badge badge-pill win badge-success">{{pastGame.status}}</span>
                    </td>
                    <td class="td"><b-button class="view" v-b-modal.modal-1  game="'pastGame'" @click="sendInfo(pastGame)">View</b-button></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div>
            <b-modal id="modal-1" title="Betslip" hide-footer>
            <html v-html="selectedGame.html"></html>
             <b-button class="mt-3 view" block @click="$bvModal.hide('modal-1')">Close Me</b-button>
            </b-modal>
          </div>
        </div>
      </div>
      <!-- End of Row -->
    </div>
  </section>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import axios from "axios";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";
import timestamp  from "time-stamp";

export default {
  components: { Loading, timestamp },
  data() {
    return {
      isLoading: false,
      fullPage: true,
      tabIndex: 0,
      pastGames: [],
      getCount: "",
      getWins: "",
      getGames: "",
      newTime:"",
      selectedGame: "",

    };
  },
  async mounted() {
          this.isLoading = true;
    await this.getBalance(),
     this.getHistory();
    // simulate AJAX
          setTimeout(() => {
            this.isLoading = false;
          }, 8000);

  },

  methods: {
    ...mapActions(["getBalance", "getSchedule", "userDetails"]),
    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ["pill-green", "text-white"];
      } else {
        return ["pill-default", "text-black"];
      }
    },
    sendInfo(pastGame) {
        this.selectedGame = pastGame;
    },
    getHistory() {
      axios
        .get(
          "https://middlewarez.herokuapp.com/api/v1/game/user/history?auth=55904993399349495bb6fdd&as=history&username=" + this.userDetails.username
        )
        .then(response => {
         // this.pastGames
          const history = response.data.data.result_desc;
          const newArray = history.map(function (item) {
    return {
        Bet_Details: item.Bet_Details,
        Stake: item.Stake,
        Possible_Winnings: item.Possible_Winnings,
        Possible_Bonus: item.Possible_Bonus,
        status: item.status,
        Date_Placed: item.Date_Placed,
        html: item.html,
        ID: item.ID
    };
});
          this.pastGames = newArray
          const count = response.data.data.result_desc;
          this.getGames = count.length;
          this.getCount = count.reduce(function(n, count) {
            return n + (count.status == "Pending");
          }, 0);
          this.getWins = count.reduce(function(n, count) {
            return n + (count.status == "Won");
          }, 0);
          
        })
        .catch(error => {
          console.log(error);
        });
            this.newTime = timestamp();// unix timestamp

    }
  },
  computed: {
    itemsSuccess: function() {
    return this.pastGames.filter(function(item) {
      return ["Naira","Rich", "Success", "Kano", "Cash", "LG", "Millions", "MSP", "Winna", "MidWeek", "National", "Bonanza", "Fortune", "Naija Gold"].indexOf(item.Bet_Details) != -1
   })
  },
    ...mapState(["userBalance", "userDetails"])
  }
};
</script>
